import { Component, NgZone } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, Events } from 'ionic-angular';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { Storage } from '@ionic/storage';
import { Network } from '@ionic-native/network';
import { Constants } from '../../services/constants';
import { ApiServiceProvider } from '../../providers/api-service/api-service';
import { HelperService } from '../../providers/helper';

//import { Toast } from '@ionic-native/toast';

/**
 * Generated class for the AccountPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-account',
  templateUrl: 'account.html',
})
export class AccountPage {
  public balanceForm : FormGroup;
  public balance : string;
  public showForm : boolean = false;
  public isDeviceOnline : boolean;
  public responseData : any;
  public payResultText : string;
  public showPaymentResultText : boolean;

  constructor(public navCtrl: NavController, private storage: Storage, private formBuilder: FormBuilder, private network: Network, public navParams: NavParams, public loadingCtrl: LoadingController, 
    public apiService : ApiServiceProvider, public events : Events, private zone: NgZone, private helper: HelperService /*, private toast: Toast*/) {
    this.showPaymentResultText = false;
    this.payResultText = "";
    
    this.balance = this.helper.balance;
    this.balanceForm = this.formBuilder.group({
        amount:['', Validators.required]
    });
    this.isDeviceOnline = true;
    // watch network for a disconnect
    this.network.onDisconnect().subscribe(() => {
      this.zone.run(() => {
        this.isDeviceOnline = false;
      });
    });
    // watch network for a connection
    this.network.onConnect().subscribe(() => {
      this.zone.run(() => {
        this.isDeviceOnline = true;
      });
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AccountPage');
  }
  
  showPayMoreBalanceForm(){
    this.showForm = true;
  }
  
  attempToPayMoreBalance(){
    let loading = this.loadingCtrl.create({
      content: 'Espere un momento...'
    });
    loading.present();
    this.showPaymentResultText = false;
    
    this.storage.get(Constants.userLoggedInIdKey).then(val => {
        var formData = new FormData();
        formData.append('a', this.balanceForm.value.amount);
        formData.append('u', val);
        
        this.apiService.postData(formData, 'paymorebalance.php').then(result => {
            loading.dismiss();
            this.responseData = result;
            this.showPaymentResultText = true;
            
            if (this.responseData.status == "ok") {
                var balanceVal = '$' + this.responseData.balance;
                this.payResultText = 'Su transaccion se ha hecho con exito.\n' + 
                                     'Su nuevo saldo es ' + balanceVal;
                this.events.publish('setBalance', balanceVal);
                this.balance = balanceVal;
            }
            else{
                this.payResultText = this.responseData.msg;
            }
        }, (err) => {
           this.payResultText = err.msg;
        });
    });
  }
}



